
import XButton from 'vux/src/components/x-button/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import { Operation, Confirmation } from "@/servers/api";
export default {
name: "",
props: ["orderData"],
data() {
return {
types: ["ios"]
};
},

components: {
XButton,
Spinner,
Group,
Cell
},
filters: {
showOrderState(status) {
switch (status) {
case "0":
return "待付款";
break;
case "1":
return "待发货";
break;
case "2":
return "待收货";
break;
case "3":
return "已完成";
break;
default:
return "";
}
}
},
computed: {},

beforeMount() {},

mounted() {},

methods: {
sureOrder(id,index) {
this.$vux.confirm.show({
title: "提示",
content: "确定收货吗？",
onCancel: () => {
console.log("用户点击取消");
},
onConfirm: () => {
this.postSureOrder(id, index);
}
});
},
postSureOrder(id,index) {
Confirmation({
order_id: id,
})
.then(res => {
console.log(res);
if(res.result === 1){
this.$emit("sureOrder", index);
}
})
.catch(err => {
console.log(err);
});
},
watchExpressInfo(id) {
this.$router.push("/expressinfo?id=" + id);
},
delGoods(id, index) {
this.$vux.confirm.show({
title: "提示",
content: "确定要删除该订单吗？",
onCancel: () => {
console.log("用户点击取消");
},
onConfirm: () => {
this.deleteItem(id, index);
}
});
},
deleteItem(id, index) {
Operation({
order_id: id,
status: "-1"
}).then(res => {
console.log(res);
if (res.result === 1) {
this.$emit("delData", index);
}
});
}
},

watch: {}
};



